// Name:                       Icon
//
// Description:                Define style for icon
//
// Adapted from http://fortawesome.github.com/Font-Awesome (Version 4.2.0)
//
// Component:                `am-icon-*`
//
// Sub-objects:              `am-icon-btn`
//
// Modifiers:                `am-icon-sm`
//                           `am-icon-md`
//                           `am-icon-lg`
//                           `am-icon-fw`
//                           `am-icon-spin`
//                           `am-icon-pulse`
//
// Uses:                     Animation
//
// =============================================================================

// Variables
// =============================================================================

@fa-font-path: "../fonts";
// @fa-font-path: '//dn-amui.qbox.me/font-awesome/4.5.0/fonts';
@fa-css-prefix: ~"@{ns}icon";
@fa-version: '4.6.3';
@fa-border-color: #eee;
@fa-inverse: #fff;
@fa-li-width: (30em / 14);

/* ==========================================================================
   Component: Icon
 ============================================================================ */

/* Font-face
============================================================================= */

@font-face {
  font-family: 'FontAwesome';
  src: url('@{fa-font-path}/fontawesome-webfont.eot?v=@{fa-version}');
  src: url('@{fa-font-path}/fontawesome-webfont.eot?#iefix&v=@{fa-version}') format('embedded-opentype'),
  url('@{fa-font-path}/fontawesome-webfont.woff2?v=@{fa-version}') format('woff2'),
  url('@{fa-font-path}/fontawesome-webfont.woff?v=@{fa-version}') format('woff'),
  url('@{fa-font-path}/fontawesome-webfont.ttf?v=@{fa-version}') format('truetype');
  //  src: url('@{fa-font-path}/FontAwesome.otf') format('opentype'); // used when developing fonts
  font-weight: normal;
  font-style: normal;
}

/* Component
============================================================================= */

[class*="@{ns}icon-"] {
  display: inline-block;
  font-style: normal;

  &:before {
    .am-icon-font;
  }
}

// Bordered & Pulled
// -----------------------------------------------------------------------------

.@{fa-css-prefix}-border {
  padding: .2em .25em .15em;
  border: solid .08em @fa-border-color;
  border-radius: .1em;
}

[class*="@{ns}icon-"] {
  &.@{ns}fl {
    margin-right: .3em;
  }
  &.@{ns}fr {
    margin-left: .3em;
  }
}

// Size modifiers
// =============================================================================

.@{ns}icon-sm:before {
  font-size: @icon-sm-font-size;
  vertical-align: @icon-sm-vertical-align;
}

.@{ns}icon-md:before {
  font-size: @icon-md-font-size;
  vertical-align: @icon-md-vertical-align;
}

.@{ns}icon-lg:before {
  font-size: @icon-lg-font-size;
  vertical-align: @icon-lg-vertical-align;
}

.icon-btn-size(@width: 40rem; @height: 40px; @font-size: 20px) {
  width: @width;
  height: @height;
  font-size: @font-size;
  line-height: @height;
}

/* Modifier: `.am-icon-btn`
============================================================================= */

.@{ns}icon-btn {
  box-sizing: border-box;
  display: inline-block;
  .icon-btn-size(@icon-btn-width; @icon-btn-height; @icon-btn-font-size);
  border-radius: @icon-btn-border-radius;
  background-color: @icon-btn-background;
  color: @icon-btn-color;
  text-align: center;

  .hook-icon-btn;
  /**
   * Hover
   * 1. Apply hover style also to focus state
   * 2. Remove default focus style
   */
  &:hover,
  &:focus {
    /* 1 */
    background-color: @icon-btn-hover-background;
    color: @icon-btn-hover-color;
    text-decoration: none;
    outline: none; /* 2 */
    .hook-icon-btn-hover;
  }

  /* Active */
  &:active {
    background-color: @icon-btn-active-background;
    color: @icon-btn-active-color;
    .hook-icon-btn-active;
  }

  &.@{ns}primary,
  &.@{ns}secondary,
  &.@{ns}success,
  &.@{ns}warning,
  &.@{ns}danger {
    color: @white;
  }

  &.@{ns}primary {
    background-color: @global-primary;
  }

  &.@{ns}secondary {
    background-color: @global-secondary;
  }

  &.@{ns}success {
    background-color: @global-success;
  }

  &.@{ns}warning {
    background-color: @global-warning;
  }

  &.@{ns}danger {
    background-color: @global-danger;
  }
}

.@{ns}icon-btn-sm {
  .icon-btn-size(32px; 32px; 16px);
}

.@{ns}icon-btn-lg {
  .icon-btn-size(64px; 64px; 28px);
}

/* Modifier: `.am-icon-fw` Fixed width
============================================================================= */
.@{ns}icon-fw {
  width: (20em/16);
  text-align: center;
}

