/* Modifier: `am-icon-spin`
============================================================================= */

@keyframes icon-spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(359deg);
  }
}

.am-icon-spin {
  animation: icon-spin 2s infinite linear;
}

.am-icon-pulse {
  animation: icon-spin 1s infinite steps(8);
}

// List Icons
// -----------------------------------------------------------------------------

.@{fa-css-prefix}-ul {
  padding-left: 0;
  margin-left: @fa-li-width;
  list-style-type: none;
  > li {
    position: relative;
  }
}

.@{fa-css-prefix}-li {
  position: absolute;
  left: -@fa-li-width;
  width: @fa-li-width;
  top: (2em / 14);
  text-align: center;
}

